import React, { Component } from "react";
import Hehl from './images/Hehl2.png'
import Hehl_Api from './images/Hehl_Api.png'

const PortfolioListContent = [
    {
         image: Hehl,
        category: 'Built with Angular and Typescript',
        title: 'Hehl Front End',
        link: 'https://github.com/ASOUDRY/Hehl',
        second: '/chat'
    },
    {
         image: Hehl_Api,
        category: 'Made with C# and MySQl',
        title: 'Hehl Back End',
        link: 'https://github.com/ASOUDRY/Hehl_Api',
        second: '/chat'
    },
]

class PortfolioList2 extends Component{
    render(){
        const {column , styevariation } = this.props;
        const list = PortfolioListContent.slice(0 , this.props.item);
        return(
            <React.Fragment> 
                {list.map((value , index) => (
                    <div 
                    className={`${column}`} 
                    key={index}>
                        <div 
                        className={
                            `portfolio
                            ${styevariation}`
                    }
                        >
                            <div className="thumbnail-inner" >
                                <div 
                                className={`thumbnail ${value.image}`}
                                >
                                <img src={value.image} alt='quiz'></img>
                                </div>
                                <div className={`bg-blr-image ${value.image}`}></div>
                            </div>

                            <div className="content" >
                                <div className="inner">
                                    <p>{value.category}</p>
                                    <h4><a href={value.second}>{value.title}</a></h4>

                                    <div className="portfolio-button">
                                        <a className="rn-btn" href={value.link}>View Code</a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                ))}
               
            </React.Fragment>
        )
    }
}
export default PortfolioList2;