import React, { Component } from "react";
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';

class TabsTwo extends Component{
    render(){
        let 
        tab1 = "Known Programming Languages",
        tab4 = "Education & Certification";
        const { tabStyle } = this.props
        return(
            <div>
                {/* Start Tabs Area */}
                <div className="tabs-area">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <Tabs>
                                    <TabList  className={`${tabStyle}`}>
                                        <Tab>{tab1}</Tab>
                                        <Tab>{tab4}</Tab>
                                    </TabList>

                                    <TabPanel>
                                        <div className="single-tab-content">
                                            <ul>
                                                <li>
                                                  HTML
                                                  <p>2+ years</p>
                                                </li>
                                                <li>
                                                  CSS
                                                  <p>2+ years</p>
                                                </li>
                                                <li>
                                                  JavaScript
                                                  <p>2+ years</p>
                                                </li>
                                                <li>
                                                  TypeScript
                                                  <p>2+ years</p>
                                                </li>
                                                <li>
                                                  React
                                                  <p> 2+ months </p>
                                                </li>
                                                <li>
                                                  React Native 
                                                  <p> 1+ years </p>
                                                </li>
                                                <li>
                                                  C#
                                                  <p> 5+ months </p>
                                                </li>
                                                <li>
                                                  MySQL
                                                  <p> 2+ months </p>
                                                </li>
                                                <li>
                                                  Java
                                                  <p> 1+ months </p>
                                                </li>
                                            </ul>
                                        </div>
                                    </TabPanel>

                                    <TabPanel>
                                       <div className="single-tab-content">
                                           <ul>
                                               <li>
                                                   <a href="/service">Full-Stack Immersion Certificate<span> - CareerFoundry, Berlin, Germany</span></a> 2021
                                               </li>
                                               <li>
                                                   <a href="/service">BA in Classical History<span> - University of Maryland, Baltimore County</span></a> 2018
                                               </li>
                                           </ul>
                                       </div>
                                    </TabPanel>
                                    
                                </Tabs>
                            </div>
                        </div>
                    </div>
                </div>
                {/* End Tabs Area */}
            </div>
        )
    }
}



export default TabsTwo;