import React, { Component } from "react";
import Api from './images/expense2.png'
import Movie from './images/Movie.png'
import Dog from './images/dog.png'

const PortfolioListContent = [
    {
        image: Movie,
        category: 'Made with ReactJs',
        title: 'Movie Catalogue App',
        link: 'https://github.com/ASOUDRY/movie_api',
        second: '/movie'
    },
    {
        image: Dog,
        category: 'Made with React Native',
        title: 'Dog Sitting App',
        link: 'https://github.com/ASOUDRY/furry-friends-pet-sitting',
        second: '/dog-sitting'
    },
    {
        image: Api,
        category: 'Made with C#',
        title: 'Reinbursement_Api ',
        link: 'https://github.com/ASOUDRY/Expense_Reinbursement_Api',
        second: '/dog-sitting'
    }

]

class PortfolioList extends Component{
    render(){
        const {column , styevariation } = this.props;
        const list = PortfolioListContent.slice(0 , this.props.item);
        return(
            <React.Fragment> 
                {list.map((value , index) => (
                    <div 
                    className={`${column}`} 
                    key={index}>
                        <div 
                        className={
                            `portfolio
                            ${styevariation}`
                    }
                        >
                            <div className="thumbnail-inner" >
                                <div 
                                className={`thumbnail ${value.image}`}
                                >
                                <img src={value.image} alt='quiz'></img>
                                </div>
                                <div className={`bg-blr-image ${value.image}`}></div>
                            </div>

                            <div className="content" >
                                <div className="inner">
                                    <p>{value.category}</p>
                                    <h4><a href={value.second}>{value.title}</a></h4>

                                    <div className="portfolio-button">
                                        <a className="rn-btn" href={value.link}>View Code</a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                ))}
               
            </React.Fragment>
        )
    }
}
export default PortfolioList;