import React from 'react';
import ScrollToTop from 'react-scroll-up';
import { FiChevronUp } from "react-icons/fi";
import Helmet from "../component/Helmet";
import HeaderThree from "../component/HeaderThree";
import FooterTwo from "../component/FooterTwo";
import TabTwo from "../elements/TabTwo";
import ContactThree from "../elements/ContactThree";
import PortfolioList from "../elements/PortfolioList";
import PortfolioList2 from '../elements/PortfolioList2';
import Profile from "../elements/images/profile.jpg"


import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'

const SlideList = [
    {
        textPosition: 'text-left',
        category: 'Full-Stack Software Developer',
        title: 'Hello, I’m <span>Alexander</span> Welcome to my World.',
        description: '',
        buttonText: '',
        buttonLink: ''
    }
]
const PortfolioLandingDark = () => {
    let title = 'About Me',
        description = "Hello, My name is Alexander Soudry and I am a Full Stack Software Developer with experience building mobile first and mobile full stack applications. I first gained an interest in software development during my final year of university where I took a computer science course in C+. The satisfaction I felt from writing and compiling even the simplest programs convinced me to pursue software development as a career. I have experience in pitching ideas for projects, planning out application functionality, and working with team members to develop small applications or components of larger applications within a tight window. I am always learning and I love to pick up new skills and apply them to my personal projects. rapidly picking up and utilizing new skills. I am confident I will be a invaluable asset for you and your team.";
    return (
        <div 
       
        >
            <Helmet pageTitle="Alexander Soudry's Website" />

            <HeaderThree homeLink="/" logo="symbol-dark" color="color-black"/>
            {/* Start Slider Area   */}
            <div id="home" className="fix">
                <div className="slider-wrapper">
                    {/* Start Single Slide */}
                    {SlideList.map((value , index) => (
                        <div 
                        className="slide personal-portfolio-slider slider-paralax slider-style-3 d-flex align-items-center justify-content-center" 
                        key={index}>
                              <div className="container">
                                <div className="row">
                                    <div className="col-lg-12">
                                        <div 
                                        className={`inner ${value.textPosition}`}
                                        >
                                            {value.category ? <span>{value.category}</span> : ''}
                                            {value.title ? <h1 className="title" dangerouslySetInnerHTML={{__html: value.title}}></h1> : ''}
                                            {value.description ? <p className="description">{value.description}</p> : ''}
                                            {value.buttonText ? <div className="slide-btn"><a className="rn-button-style--2 btn-primary-color" href={`${value.buttonLink}`}>{value.buttonText}</a></div> : ''}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    ))}
                    {/* End Single Slide */}
                </div>
            </div>
            {/* End Slider Area   */} 

            {/* Start About Area */}
            <div id="about" className="fix">
                <div className="about-area ptb--120  bg_color--1">
                    <div className="about-wrapper">
                        <div className="container">
                            <div className="row row--35 align-items-center">
                                <div className="col-lg-5">
                                    <div className="thumbnail">
                                    <img className="w-100" src={Profile} alt="Profile"/>
                                    </div>
                                </div>
                                <div className="col-lg-7">
                                    <div className="about-inner inner">
                                        <div className="section-title">
                                            <h2 className="title">{title}</h2>
                                            <p className="description">{description}</p>
                                        </div>
                                        <div className="row mt--30">
                                            <TabTwo tabStyle="tab-style--1" />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            
            {/* End About Area */}

            {/* Start Portfolio Area */}
            <div id="portfolio" className="fix">
                <div className="portfolio-area ptb--120 bg_color--1">
                    <div className="portfolio-sacousel-inner">
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-12">
                                    <div className="section-title text-center service-style--3 mb--30 mb_sm--0">
                                        <h2 className="title">My Current Project</h2>
                                        <p> Hehl is a dark fantasy web based text game, built with Angular, C# and MySQl. Users will be able to create a account and explore the world of Hehl, talking with npcs, solving mysteries and fighting monsters. The game will be hosted on my github account, while the Api is hosted with Microsoft Azure.</p>
                                    </div>
                                </div>
                            </div>
                                        
                            <Container
                               style={{ marginBottom: '100px' }} 
                            >
                                <Row className="justify-content-md-center" 
                               
                                >
                                        <PortfolioList2 styevariation="text-center mt--40"
                                         
                                        column="col-lg-4 col-md-6 col-sm-6 col-12" 
                                        item="2" />      
                                </Row> 

</Container>
                                <div className="section-title text-center service-style--3 mb--30 mb_sm--0">
                                        <h2 className="title"> Some other Cool Projects I have worked on</h2>
                                    </div>
                              <Container>        
                                <Row className="justify-content-md-center">
                                        <PortfolioList styevariation="text-center mt--40" 
                                        column="col-lg-4 col-md-6 col-sm-6 col-12" 
                                        item="3" />      
                                </Row> 
                            </Container> 
                            <div className="row">
                                <div className="col-lg-12">
                                    <div className="view-more-btn mt--60 mt_sm--30 text-center">
                                    <a className="rn-button-style--2 btn-solid" href="https://github.com/ASOUDRY"><span>More Projects at my Github</span></a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {/* End Portfolio Area */}

            {/* Start COntact Area */}
            <div id="contact" className="fix">
                <div className="rn-contact-area ptb--120 bg_color--1">
                    <ContactThree contactTitle="Hire Me." />
                </div>
            </div>
            {/* End COntact Area */}

            <FooterTwo />
            {/* Start Back To Top */}
            <div className="backto-top">
                <ScrollToTop showUnder={160}>
                    <FiChevronUp />
                </ScrollToTop>
            </div>
            {/* End Back To Top */}
            
        </div>
    )
}






export default PortfolioLandingDark;
